const ImageCard: FC<{ src: string; onClick: () => void }> = ({
  src,
  onClick,
}) => {
  return (
    <div className="w-full m-1 md:m-2 bg-secondary relative">
      <div
        className="absolute w-full h-full hover:bg-primary hover:opacity-20 opacity-0 cursor-pointer"
        onClick={onClick}
      >
        <FullScreenIcon />
      </div>
      <img
        alt="gallery"
        className="block object-scale-down w-full h-full rounded-lg"
        src={src}
      />
    </div>
  );
};

export const Gallery: FC<GalleryProps> = () => {
  const navigate = useNavigate();
  const match = useMatch("gallery");
  const ref = useRef<ReactImageGallery | null>(null);
  const handleImageClick = (idx: number) => {
    navigate("gallery");
    ref.current?.slideToIndex(idx);
  };

  return (
    <section className="overflow-hidden text-gray-700 ">
      <div className="container px-5 py-2 mx-auto lg:pt-12 lg:px-32">
        <div className="flex flex-wrap -m-1 md:-m-2">
          {_images.map((image, idx) => (
            <div className="flex flex-wrap w-1/4" key={idx}>
              <ImageCard
                src={image.original + "?" + idx}
                onClick={() => handleImageClick(idx)}
              />
            </div>
          ))}
        </div>
      </div>
      <Modal open={!!match} onCloseClick={() => navigate("")}>
        <ImageGallery
          ref={ref}
          showFullscreenButton={false}
          showPlayButton={false}
          items={_images}
          additionalClass="p-10 h-screen max-h-screen w-full"
        />
      </Modal>
    </section>
  );
};
